@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  justify-content: center;
  align-items: center;
  font-family: "Lexend Deca", sans-serif;
  font-size: 0.9375rem;
  background: #090b1a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:active,
a:visited {
  text-decoration: none;
}

.attribution {
  font-size: 11px;
  text-align: center;
  position: absolute;
  color: rgba(255, 255, 255, 0.6);
  bottom: 0;
}
.attribution a {
  color: #3e52a3;
}

.card {
  background: #1b1938;
}

.container {
  display: flex;
}